export default {
	onLoad() {
		// uni.setStorageSync('loginFlag', 1)
		if(uni.getStorageSync('user_phone')) {
			this.phone = uni.getStorageSync('user_phone')
		}
		let data = uni.getSystemInfoSync();
		this.platform = data.platform;
		console.log(this.platform == 'android')
	},
	onHide() {
		uni.setStorageSync('loginFlag', 1)
	},
	onShow() {
		let data = uni.getSystemInfoSync();
		this.platform = data.platform;
	},
	data () {
		return{
			phone: '',
			pwd: '',
			scrollIndex:1,
			cid: '',
			uniId: '',
			nickName: '',
			avatarUrl: '',
			platform: ''
		}
	},
	methods: {
		handleScrollList(scrollIndex){
			this.scrollIndex = scrollIndex
			this.pwd = ""
		},
		//到注册页面
		goToRegister () {
			uni.navigateTo({
				url: '../register/register'
			})
		},
		//到修改密码
		goToForgetPwd () {
			uni.navigateTo({
				url:'../my/changePwd?status=0'
			})
		},
		//登录
		async login () {
			
			if (this.phone.trim() === '' || this.pwd.trim() === "") {
				uni.showToast({
					icon: 'none',
					title: '请先输入手机或密码'
				})
				return
			}
			this.cid = plus.push.getClientInfo().clientid
			let res = await this.$fetch(this.$api.customerLogin,{loginName: this.phone,password: this.pwd,cid:this.cid},'POST',"Form")
			this.resMsg = res.msg
			if (res.code == 0) {
				uni.setStorageSync('token',res.data.token)
				uni.setStorageSync('redInfoFlag', 1)
				uni.showToast({
					icon:'success',
					title: '登录成功'
				})
				setTimeout(() => {
					uni.switchTab({
						url:'../my/my'
					})
				},1500)
			} else {
				uni.showToast({
					icon:'none',
					title: res.msg
				})
				uni.setStorageSync('redInfoFlag', 0)
			}
			
		},
		wxLogin () {
			uni.showLoading({
				title: '正在登陆中...'
			})
			
			uni.login({
			  provider: 'weixin',
			  success:  async(loginRes) => {
				  console.log(loginRes.authResult)
				  this.uniId = loginRes.authResult.unionid
			    // 获取用户信息
			    uni.getUserInfo({
			      provider: 'weixin',
			      success:  (infoRes) => {
			        
					this.nickName = infoRes.userInfo.nickName
					this.avatarUrl = infoRes.userInfo.avatarUrl

			      }
			    });
				uni.hideLoading()
				let res = await this.$fetch(this.$api.customerWx,{cid:this.cid,unionId:loginRes.authResult.unionid},'POST','FORM')
				
				if (res.code !== 0) {
					uni.showToast({
						icon:'none',
					    title: '暂未绑定手机号',
					});
					setTimeout(()=>{
						uni.navigateTo({
							url: '../register/register?wxlogin=' + 1 + '&unionId=' + this.uniId + '&nickName=' + this.nickName + '&avatarUrl=' + this.avatarUrl
						})
					},1000)
				} else {
					uni.setStorageSync('token',res.data.token)
					
					uni.switchTab({
						url: '../my/my'
					})
					
					
				}
				
			   
			  }
			});
		}
	}
}
